<template>
  <div id="palm"> 
    <div class="container-fluid container_fluid">
        <div class="row">
            <!-- <div class="col-md-12 palm_top_nav">
                <img :src="webPalm" class="palm_image">
            </div> -->
            <div class="col-md-12 palm_top_image" v-if="slientWidth > 750">
                <img :src="imgs.webImage" class="palm_image_img" v-for="(imgs,key) in webPalmImageList" :key="key">
            </div>

            <div class="col-md-12 palm_top_image" v-if="slientWidth < 750">
                <img :src="imgs.appImage" class="palm_image_img" v-for="(imgs,key) in appPalmImageList" :key="key">
            </div>
        </div>
    </div>
  </div>
</template>

<script>

export default {
  data(){
    return{
        slientWidth:document.body.clientWidth,
        webPalmImageList:[
            {webImage:'../../../static/image/ProductsImage/PalmImage/plamImage1.png',},
            {webImage:'../../../static/image/ProductsImage/PalmImage/plamImage2.png',},
            {webImage:'../../../static/image/ProductsImage/PalmImage/plamImage3.png',},
            {webImage:'../../../static/image/ProductsImage/PalmImage/plamImage4.png',},
            {webImage:'../../../static/image/ProductsImage/PalmImage/plamImage5.png',},
            {webImage:'../../../static/image/ProductsImage/PalmImage/plamImage6.png',},
        ],
        appPalmImageList:[
            {appImage:'../../../static/image/ProductsImage/PalmImage/appPlamImage1.png',},
            {appImage:'../../../static/image/ProductsImage/PalmImage/appPlamImage2.png',},
            {appImage:'../../../static/image/ProductsImage/PalmImage/appPlamImage3.png',},
            {appImage:'../../../static/image/ProductsImage/PalmImage/appPlamImage4.png',},
            {appImage:'../../../static/image/ProductsImage/PalmImage/appPlamImage5.png',},
            {appImage:'../../../static/image/ProductsImage/PalmImage/appPlamImage6.png',},
        ],

    }
  },
  created(){
      this.slientWidth = document.body.clientWidth
    if(document.body.clientWidth > 750){
      this.webPalm = '../../static/image/ProductsImage/webPalm.png'
      this.webPalmImageList
    }else{
      this.webPalm = '../../static/image/ProductsImage/appPalm.png'
      this.appPalmImageList
    }
  },
  methods:{
    
  },
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@media screen and (min-width: 750px){
    .palm_image{
        width: 100%;
        min-height: 150px;
    }
    /* .palm_top_image{

    } */
    .palm_image_img{
        width: 100%;
    }
}

/* 移动端特殊样式 */
@media screen and (max-width: 750px){
    .palm_image{
        width: 20%;
    }
    .palm_image_img{
        width: 100%;
    }
}

</style>



